import * as React from "react";
import { Box, styled, Fab, LinearProgress } from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";

const LinearProgressNoAnimation = styled(LinearProgress)({
  "*": { transition: "some" }, //none here kills the transition and some allows it
});

const IngredientColumn = ({ inventory, addBakers, totBakers }) => {
  return (
    <Box>
      <Box>Bakers : {totBakers}</Box>
      {inventory.map((inventoryItem) => {
        return (
          <Box sx={{ flexGrow: 1 }}>
            <Grid container spacing={1}>
              <Grid xs={8}>
                <Box sx={{ paddingTop: 2 }}>
                  {" "}
                  {inventoryItem.ingredient.label} : {inventoryItem.quantity}{" "}
                </Box>
              </Grid>
              <Grid xs={4}>
                <Fab
                  color="secondary"
                  size="small"
                  onClick={() => addBakers(inventoryItem.ingredient.id, 1)}
                  disabled={totBakers < 1}
                >
                  +
                </Fab>{" "}
                &nbsp;
                <Fab
                  color="secondary"
                  size="small"
                  onClick={() => addBakers(inventoryItem.ingredient.id, -1)}
                  disabled={inventoryItem.numBakers < 1}
                >
                  -
                </Fab>
                <Box>Bakers : {inventoryItem.numBakers}</Box>
              </Grid>
              <Grid xs={12}>
                <LinearProgressNoAnimation
                  variant="determinate"
                  value={inventoryItem.currentProgress}
                />
              </Grid>
            </Grid>
          </Box>
        );
      })}
    </Box>
  );
};
export default IngredientColumn;
