import * as React from "react";
import { Box, Button, Divider, Chip } from "@mui/material";

// import { Meals } from "./helpers/SharedTypes";
import { allMeals } from "./helpers/allResources.ts";
// import { InventoryItem } from "./helper/SharedTypes";
// import { allIngredients } from "./helpers/allResources";
import Grid from "@mui/material/Unstable_Grid2";

const GameColumn = ({
  inventory,
  incrementInventory,
  numDollars,
  changeDollars,
}) => {
  const canMakeRecipe = (mealIngredients, multiplier = 1) => {
    let canMakeMeal = true;
    mealIngredients.forEach((mealIngredient) => {
      const currentSupply = inventory.find(
        (item) => item.ingredient.id === mealIngredient.resourceId
      );
      if (currentSupply.quantity < mealIngredient.numRequired * multiplier) {
        canMakeMeal = false;
      }
    });
    return canMakeMeal;
  };

  const makeMeals = (resourceIds, changeDollarAmount, multiplier = 1) => {
    resourceIds.forEach((resourceId) => {
      incrementInventory(
        resourceId.resourceId,
        -resourceId.numRequired * multiplier
      );
    });
    changeDollars(changeDollarAmount);
  };

  return (
    <Box>
      <Box> Dollars : ${numDollars} </Box>
      {allMeals.map((meals) => {
        return (
          <Box sx={{ margin: 2 }}>
            <Divider sx={{ marginBottom: 2 }}>
              {" "}
              <Chip label={meals.label} />
            </Divider>
            <Button
              variant="contained"
              disabled={!canMakeRecipe(meals.mealIngredients)}
              disableElevation
              onClick={() => makeMeals(meals.mealIngredients, meals.salePrice)}
            >
              x1
            </Button>
            <Button
              variant="contained"
              disabled={!canMakeRecipe(meals.mealIngredients, 10)}
              disableElevation
              onClick={() =>
                makeMeals(meals.mealIngredients, meals.salePrice * 10, 10)
              }
            >
              x10
            </Button>
            <Button
              variant="contained"
              disabled={!canMakeRecipe(meals.mealIngredients, 100)}
              disableElevation
              onClick={() =>
                makeMeals(meals.mealIngredients, meals.salePrice * 100, 100)
              }
            >
              x100
            </Button>
            <Grid container spacing={0}>
              <Grid xs={6}>
                {meals.mealIngredients.map((mealIngredients) => {
                  return (
                    <Grid>
                      {mealIngredients.resourceLabel} :{" "}
                      {mealIngredients.numRequired}
                    </Grid>
                  );
                })}
              </Grid>
              <Grid xs={6}>
                <Grid> Sale Price x1 : ${meals.salePrice} </Grid>
                <Grid> Sale Price x10 : ${meals.salePrice * 10} </Grid>
                <Grid> Sale Price x100 : ${meals.salePrice * 100} </Grid>
              </Grid>
            </Grid>
          </Box>
        );
      })}
    </Box>
  );
};
export default GameColumn;
