import "./App.css";
import * as React from "react";
import { Routes, Route } from "react-router-dom";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { red, grey } from "@mui/material/colors/";
import { Paper } from "@mui/material";

import Landing from "./components/Landing.tsx";
import Resume from "./components/resume/Resume.tsx";
import BakeryGame from "./components/projects/Bakery_Game/BakeryGame.tsx";
import TikTakToe from "./components/projects/TikTakToe/Ultimate.tsx";
import Misc from "./components/projects/Misc.tsx";

function App() {
  const [colorMode, setColorMode] = React.useState("dark");

  const theme = createTheme({
    palette: {
      primary: { main: red[900] },
      secondary: { main: grey[800] },

      error: { main: red[600] },
      warning: { main: red[600] },
      info: { main: red[600] },
      success: { main: grey[200] },
      text: { primary: grey[300] },
      mode: colorMode,
    },
  });

  return (
    <div className="App">
      <ThemeProvider theme={theme} color="textPrimary">
        <Paper sx={{ minHeight: "100vh" }}>
          <Routes>
            <Route path="/" element={<Landing />} />
            <Route path="resume" element={<Resume />} />
            <Route path="bakeryGame" element={<BakeryGame />} />
            <Route path="tiktaktoe" element={<TikTakToe />} />
            <Route path="misc" element={<Misc />} />
            {/* <Route path="*" element={<NoPage />} /> */}
          </Routes>
        </Paper>
      </ThemeProvider>
    </div>
  );
}

export default App;
