import React from "react";
import { Box } from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";
import DetailsColumn from "./DetailsColumn";
import SpendColumn from "./SpendColumn.js";
import IngredientColumn from "./IngredientColumn";
import { InventoryItem } from "./helpers/SharedTypes.ts";
import { allIngredients } from "./helpers/allResources.ts";
import Navbar from "../../Navbar.tsx";

const BakeryGame = () => {
  const [inventory, setInventory] = React.useState<InventoryItem[]>(
    allIngredients.map((ingredient) => {
      return {
        ingredient: ingredient,
        quantity: 0,
        numBakers: 0,
        currentProgress: 0,
        amountEarned: 1,
      };
    })
  );

  const [totBakers, setTotBakers] = React.useState(2);
  const [numDollars, setNumDollars] = React.useState(50);
  const [progressBarSpeed, setProgressBarSpeed] = React.useState(2);
  const [amountMultiplier, setAmountMiltiplier] = React.useState(1);

  const changeDollars = (numToChange: number) => {
    setNumDollars((numDollars) => numDollars + numToChange);
  };

  const incrementInventory = (
    resourceId: string,
    amountToChange: number,
    amountMultiplier = 1
  ) => {
    setInventory((prevInventory) => {
      const newInventory: InventoryItem[] = JSON.parse(
        JSON.stringify(prevInventory)
      );
      const resourceToIncrement = newInventory.find(
        (inventoryItem) => inventoryItem.ingredient.id === resourceId
      );
      if (amountToChange < 0) {
        resourceToIncrement.quantity += amountToChange;
      } else {
        resourceToIncrement.quantity += amountToChange * amountMultiplier;
      }

      return newInventory;
    });
  };

  const addBakers = (resourceId: string, numBakers: number) => {
    setInventory((prevInventory) => {
      const newInventory: InventoryItem[] = JSON.parse(
        JSON.stringify(prevInventory)
      );
      const resourceToIncrement = newInventory.find(
        (inventoryItem) => inventoryItem.ingredient.id === resourceId
      );
      resourceToIncrement.numBakers += numBakers;
      return newInventory;
    });
    setTotBakers((prevNumBakers) => prevNumBakers - numBakers);
  };

  const increaseTotBakers = (amountToIncrement = 1) => {
    setTotBakers((totBakers) => totBakers + amountToIncrement);
  };

  const increaseProgressBarSpeed = (amountToIncrement = 0.25) => {
    setProgressBarSpeed(
      (progressBarSpeed) => progressBarSpeed + amountToIncrement
    );
  };

  const increaseAmountMultiplier = (amountToIncrement = 1) => {
    setAmountMiltiplier(
      (amountMultiplier) => amountMultiplier + amountToIncrement
    );
  };

  // The following controls the Progress Bar

  React.useEffect(() => {
    const timer = setInterval(() => {
      setInventory((prevInventory) => {
        const newInventory: InventoryItem[] = JSON.parse(
          JSON.stringify(prevInventory)
        );
        prevInventory.forEach((item) => {
          const resourceToIncrement = newInventory.find(
            (inventoryItem) =>
              inventoryItem.ingredient.id === item.ingredient.id
          );
          let newProgress =
            resourceToIncrement.currentProgress +
            progressBarSpeed * resourceToIncrement.numBakers;
          while (newProgress > 101) {
            newProgress -= 100;
            incrementInventory(
              item.ingredient.id,
              item.amountEarned,
              amountMultiplier
            );
            newProgress = 0;
          }
          resourceToIncrement.currentProgress = newProgress;
        });
        return newInventory;
      });
    }, 380);

    return () => {
      clearInterval(timer);
    };
  }, [amountMultiplier, progressBarSpeed]);

  return (
    <Box sx={{ flexGrow: 1 }}>
      <Box id="navbar">
        <Navbar />
      </Box>
      <br />
      <br />
      <Grid container spacing={3}>
        <Grid xs={4}>
          <IngredientColumn
            inventory={inventory}
            addBakers={addBakers}
            totBakers={totBakers}
          />
        </Grid>
        <Grid xs={4}>
          <SpendColumn
            inventory={inventory}
            incrementInventory={incrementInventory}
            numDollars={numDollars}
            changeDollars={changeDollars}
          />
        </Grid>
        <Grid xs={4}>
          <DetailsColumn
            numDollars={numDollars}
            changeDollars={changeDollars}
            increaseTotBakers={increaseTotBakers}
            totBakers={totBakers}
            increaseProgressBarSpeed={increaseProgressBarSpeed}
            progressBarSpeed={progressBarSpeed}
            increaseAmountMultiplier={increaseAmountMultiplier}
            amountMultiplier={amountMultiplier}
          />
        </Grid>
      </Grid>
    </Box>
  );
};
export default BakeryGame;
