import React from "react";
import Navbar from "../Navbar.tsx";
import { Box, Typography, LinearProgress } from "@mui/material";

function misc() {
  /* Sets up to display the current date */
  let today = new Date();
  let dd = String(today.getDate()).padStart(2, "0");
  let mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
  let yyyy = today.getFullYear();

  let currentDate = mm + "/" + dd + "/" + yyyy;

  /* Sets up to display the day of the year it is (0 - 365 ? 366) */
  const dayOfYear = (date) =>
    Math.floor(
      (date - new Date(date.getFullYear(), 0, 0)) / 1000 / 60 / 60 / 24
    );

  /* gets total days in the year, taking account of leap year */
  function days_of_a_year(year) {
    return isLeapYear(year) ? 366 : 365;
  }
  function isLeapYear(year) {
    return year % 400 === 0 || (year % 100 !== 0 && year % 4 === 0);
  }

  /* Sets up the Linear Progress bar to add in custom MAX and MIN ranges */
  const MIN = 0;
  const MAX = days_of_a_year(yyyy);
  const normalise = (value) => ((value - MIN) * 100) / (MAX - MIN);

  /* Adds the correct ending to the day of the year */
  function nthDayEnding(day) {
    if (day % 10 === 1) return "st";
    if (day % 10 === 2) return "nd";
    if (day % 10 === 3) return "rd";
    return "th";
  }

  /*  */
  let perDone = Math.round(normalise(dayOfYear(today)));

  return (
    <Box>
      <Box id="navbar">
        <Navbar />
      </Box>
      <Typography>The Date is : {currentDate} </Typography>
      <Typography>
        This is the {dayOfYear(today)}
        {nthDayEnding(dayOfYear(today))} day of the year
      </Typography>
      <LinearProgress
        sx={{ height: "10px", width: "30%", margin: "auto" }}
        variant="determinate"
        value={normalise(dayOfYear(today))}
      />
      <Typography> The Year is {perDone}% over</Typography>
    </Box>
  );
}

export default misc;
