import * as React from "react";
import { Box, Stack, Button } from "@mui/material";

const DeailsColumn = ({
  numDollars,
  changeDollars,
  increaseTotBakers,
  totBakers,
  increaseProgressBarSpeed,
  progressBarSpeed,
  increaseAmountMultiplier,
  amountMultiplier,
}) => {
  const [upgradeCostBakers, setUpgradeCostBakers] = React.useState(10);
  const [upgradeCostSpeed, setUpgradeCostSpeed] = React.useState(100);
  const [upgradeCostMulti, setUpgradeCostMulti] = React.useState(50);

  const upgradeBakers = () => {
    const oldUpgraceCostBakers = upgradeCostBakers;

    changeDollars(-oldUpgraceCostBakers);
    setUpgradeCostBakers((upgradeCostBakers) =>
      Math.round(upgradeCostBakers * 1.25)
    );
    increaseTotBakers(1);
  };

  const upgradeSpeed = () => {
    const oldUpgraceCostSpeed = upgradeCostSpeed;

    changeDollars(-oldUpgraceCostSpeed);
    setUpgradeCostSpeed((upgradeCostSpeed) =>
      (upgradeCostSpeed * 1.5).toFixed(0)
    );
    increaseProgressBarSpeed();
  };

  const upgradeMultiplier = () => {
    const oldUpgraceCostMulti = upgradeCostMulti;

    changeDollars(-oldUpgraceCostMulti);
    setUpgradeCostMulti((upgradeCostMulti) =>
      (upgradeCostMulti * 1.1).toFixed(0)
    );
    increaseAmountMultiplier();
  };

  return (
    <Box sx={{ width: "50%", alignContent: "center" }}>
      <Stack
        spacing={2}
        sx={{
          display: "flex",
          "& > *": {
            m: 1,
          },
        }}
      >
        <Box>Current Baker Speed : {progressBarSpeed}</Box>
        <Box>Current Multplier : {amountMultiplier}</Box>
        <Button
          onClick={() => upgradeBakers()}
          disabled={numDollars < upgradeCostBakers}
        >
          Buy A Baker : ${upgradeCostBakers}
        </Button>
        <Button
          onClick={() => upgradeSpeed()}
          disabled={numDollars < upgradeCostSpeed}
        >
          Upgrade Baker Speed : ${upgradeCostSpeed}
        </Button>
        <Button
          onClick={() => upgradeMultiplier()}
          disabled={numDollars < upgradeCostMulti}
        >
          Upgrade Resource Multiplier : ${upgradeCostMulti}
        </Button>
      </Stack>
    </Box>
  );
};
export default DeailsColumn;
